
.article-item{
    margin: 5px 0px 5px 0px;
    padding: 23px 15px 23px 15px;
}
.article-item-hold{
    margin: 5px 0px 5px 0px;
    padding: 23px 15px 23px 15px;
    background-color: rgb(245, 245, 245);
    border-radius: 5px;
}
.article-item-info {
    float:right;
}
.article-item a {
  text-decoration:none;
  color: rgb(182, 182, 182);
}
.article-item-hold a {
  text-decoration:none;
  color: rgb(182, 182, 182);
}
.article-item-hold a:hover{
    color: black;
    text-decoration:none;
}

